<template>
    <me-search-box
    :placeholder="text"
    @query="getquery"
    fake
    @click.native="goToSearch"
    />
</template>

<script>

import MeSearchBox from 'base/search-box';
export default {
    components:{
        MeSearchBox,
    },
    data(){
        return{
            text:'hello'
        }
    },
    methods:{
        getquery(query){
           console.log(query);
            
        },

        goToSearch(){
            this.$router.push('/test2')
        }
    }
}
</script>


